.categories {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(22rem, 0fr));
    row-gap: 1rem;
    column-gap: 1rem;
    margin-top: 100px;
  }
  .category {
    height: 38rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    /* margin-right: 5px; */
    margin-bottom: 5px;
    overflow: hidden;
    padding: 0rem;
    background-color: #fff;
    border-radius: 3px;
  margin-bottom: 50px;

  
    img {
      width: 100%;
      height: 78%;
      cursor: pointer;
      object-fit: cover;
    }

    h4{
      margin-top: 10px;
    }
  }